import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { Bounds } from "../omgdc/Bounds";
import { Node } from "./Node";

export abstract class Shape extends Node {
    constructor(model: BpmnModel) {
        super(model);
    }

    Bounds: Bounds;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Bounds = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        if (this.Bounds != null) {
            BpmnModelHelper.SetAttributeValue(obj, "Bounds", this.Bounds.ConvertToJsonObject());
        }

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        if (obj.hasOwnProperty("Bounds")) {
            this.Bounds = new Bounds(this.Model);
            this.Bounds.LoadFromJsonObject(obj.Bounds);
        }
    }
}